.photos-item {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 320px;
  height: 414px;
  margin: 0;
  padding: 0;
  margin-top: 2px;
  padding: 21px 20px 10px 20px;
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.3);
}

.photos-item__a-wrap {
  width: 280px;
  height: 280px;
}

.photos-item__div-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 280px;
  height: 90px;
}

.photos-item__text-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 180px;
  height: 70px;
  margin-bottom: 7px;
}

.photos-item__likes-wrap {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-end;
  width: 90px;
  height: 31px;
}

.photos-item__title {
  display: block;
  margin: 0;
  padding: 0;
  font-size: @little-font;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  color: #000000;
}

.photos-item__author {
  display: block;
  margin: 0;
  padding: 0;
  font-size: @little-font;
  font-weight: 400;
  line-height: 24px;
  color: #333333;
}

.photos-item__likes-icon {
  width: 23px;
  height: 21px;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}

.photos-item__likes-icon path {
  fill: rgba(1, 1, 1, 0.6);
}

.photos-item__numbers {
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-left: 8px;
  font-size: @little-font;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  color: #000000;
}

.photos-item__numbers-wrap {
  margin-left: 5px;
}


@media (min-width: @tablet-width) {
  .photos-item {
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    width: 324px;
    height: 347px;
    margin: 0;
    padding: 0;
    border: none;
  }

  .photos-item__sunset {
    width: 648px;
    height: auto;
  }

  .photos-item__a-wrapsunset {
    width: 648px;
    height: 400px;
  }

  .photos-item__picture-sunset {
    width: 648px;
    height: 400px;
  }

  .photos-item__div-wrapsunset {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    padding-left: 20px;
    width: 648px;
    height: 80px;
    background-color: rgba(242, 242, 242, 1.0);
  }

  .photos-item__text-wrap {
    flex-direction: column;
    height: 62px;
  }

  .photos-item__likes-wrap {
    width: 85px;
    margin-top: 21px;
  }

  .photos-item__numbers-wrap {
    margin-left: 2px;
  }

  .photos-item__likes-icon path{
    fill: rgba(1, 1, 1, 0.3);
  }
}



